import { inject, observer } from "mobx-react";
import { useParams } from "react-router-dom";
import { useEffect } from "react";
import cn from "classnames";
import { Link } from "react-router-dom";

import styles from "./index.module.css";

const TaskViewer = inject("TasksStore")(
  observer(({ TasksStore }) => {
    const { id } = useParams();
    const { fetchTaskById, fetchedTask } = TasksStore;

    useEffect(() => {
      fetchTaskById(id);
    }, [fetchTaskById]);

    if (!fetchedTask) {
      return <span> </span>;
    }

    const { deadline } = fetchedTask;
    const expired = Date.now() > Date.parse(deadline);
    let taskStatus;
    if (fetchedTask.done) {
      taskStatus = "";
    } else {
      taskStatus = expired ? "" : " ";
    }

    const [y, m, d, time] = deadline.split(/[-T]/);
    const deadlineDateTime = `${d}.${m}.${y} ${time}`;

    return (
      <>
        <Link to="/">   </Link>
        <ul>
          <li>id : {fetchedTask.id}</li>
          <li>: {deadlineDateTime}</li>
          <li> : {taskStatus}</li>
          <li> : {fetchedTask.title}</li>
        </ul>
        <div> :</div>
        <div className={cn(styles.taskDescription)}>
          {fetchedTask.description}
        </div>
      </>
    );
  })
);

export default TaskViewer;
